body {
  margin: 20px;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

h1 {
  text-align: center;
  font-size: 2rem;
}

label {
  font-size: 1rem;
  font-weight: bold;
}

input {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 50%;
  margin: 10px auto 20px;
  padding: 10px;
  display: block;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin: 20px 0;
  font-size: 1rem;
}

table th, table td {
  text-align: left;
  border: 1px solid #ddd;
  padding: 10px;
}

table th {
  cursor: pointer;
  text-align: center;
  background-color: #f4f4f4;
  font-weight: bold;
}

table th:hover {
  background-color: #ddd;
}

table tr:nth-child(2n) {
  background-color: #f9f9f9;
}

table tr:hover {
  background-color: #f1f1f1;
}
